import { useMutation, useQuery } from "@apollo/client";
import { useEffect, useRef, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { CHANGE_PASSWORD, ME } from "../querys";

export default function ChangePassword() {
	const [currentPassword, setCurrentPassword] = useState("");
	const [newPassword, setNewPassword] = useState("");
	const [newPassword2, setNewPassword2] = useState("");
	const [errorMessage, setErrorMessage] = useState("");
	const { data: isLoginData, loading } = useQuery(ME);
	const [changePassword, { data }] = useMutation(CHANGE_PASSWORD);
	const navigate = useNavigate();
	const form = useRef();

	useEffect(() => {
		if (data?.changePassword?.success === undefined) {
			return;
		}
		if (data.changePassword.success === false) {
			setErrorMessage("  .");
			form.current.reset();
			return;
		}
		navigate("/");
	}, [data]);

	useEffect(() => {
		if (loading === false && isLoginData?.me === null) {
			navigate("/");
		}
	}, [isLoginData]);

	const onSubmit = (e) => {
		e.preventDefault();
		if (currentPassword === newPassword) {
			setErrorMessage("    .");
			return;
		}
		if (newPassword !== newPassword2) {
			setErrorMessage("   .");
			return;
		}
		changePassword({
			variables: {
				input: {
					currentPassword,
					newPassword,
				},
			},
		});
	};
	return (
		<>
			<Link to="/">
				<img src="https://jiokkae.com//img/_logo.png" />
			</Link>
			<form className="formSign" ref={form} onSubmit={onSubmit}>
				<div className="form-floating mb-3">
					<input
						type="password"
						id="cpwInput"
						className="form-control"
						placeholder=" "
						maxLength="20"
						pattern=".{8,20}"
						required
						autoFocus
						onChange={(e) => setCurrentPassword(e.target.value)}
					/>
					<label htmlFor="cpwInput"> </label>
					<small className="form-text text-muted">
						  .
					</small>
				</div>
				<div className="form-floating mb-3">
					<input
						type="password"
						id="npwInput"
						className="form-control"
						placeholder=" "
						maxLength="20"
						pattern=".{8,20}"
						autoComplete="new-password"
						required
						onChange={(e) => setNewPassword(e.target.value)}
					/>
					<label htmlFor="npwInput"> </label>
					<small className="form-text text-muted">
						8~20  .
					</small>
				</div>
				<div className="form-floating mb-3">
					<input
						type="password"
						id="npw2Input"
						className="form-control"
						placeholder="  "
						maxLength="20"
						pattern=".{8,20}"
						autoComplete="new-password"
						required
						onChange={(e) => setNewPassword2(e.target.value)}
					/>
					<label htmlFor="npw2Input">  </label>
					<small className="form-text text-muted">
						    .
					</small>
				</div>
				<div style={{ color: "red" }}>{errorMessage}</div>
				<input
					type="submit"
					className="btn btn-lg bgc-bokk signBtn"
					value=" "
				/>
			</form>
		</>
	);
}
